<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 输入机器人编号 start -->
          <el-form-item label="" prop="robotCode">
            <el-input
              class="input-name"
              v-model="searchValidateForm.robotCode"
              placeholder="机器人编号"
              type="number"
            ></el-input>
          </el-form-item>
          <!-- 输入机器人编号 end -->

          <!-- 选择景区名称 start -->
          <el-form-item label="" prop="spotId">
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择景区"
              @change="onChangeSearchSpot"
              clearable
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.SCENIC_SPOT_NAME"
                :value="item.SCENIC_SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 选择景区名称 end -->

          <!-- 选择故障名称 start -->
          <!-- <el-form-item label="" prop="faultId">
            <el-input
              class="input-name"
              v-model="searchValidateForm.faultName"
              placeholder="故障名称"
            ></el-input>
          </el-form-item> -->
          <!-- 选择故障名称 end -->

          <!-- 输入申请单号 start -->
          <el-form-item label="" prop="faultNumber">
            <el-input
              class="input-name"
              v-model="searchValidateForm.faultNumber"
              placeholder="申请单号"
            ></el-input>
          </el-form-item>
          <!-- 输入申请单号 end -->

          <!-- 选择维修人员 start -->
          <!-- <el-form-item label="" prop="userId">
            <el-select
              v-model="searchValidateForm.userId"
              filterable
              placeholder="请选择维修人员"
              @change="onChangeSearchUser"
              clearable
            >
              <el-option
                v-for="(item, index) in searchUserOptions"
                :key="index"
                :label="item.userName"
                :value="item.userId"
              >
              </el-option>
            </el-select>
          </el-form-item> -->
          <!-- 选择维修人员 end -->

          <!-- 输入申请起止时间 start -->
          <el-form-item label="" prop="startEndDate" style="width: 350px">
            <el-date-picker
              v-model="startEndDate"
              unlink-panels
              :type="dateType"
              range-separator="至"
              start-placeholder="申请开始日期"
              end-placeholder="申请结束日期"
              value-format="yyyy-MM-dd"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 输入申请起止时间 end -->
        </el-form>

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 新增 start -->
        <!-- <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">新增</el-button> -->
        <!-- 新增 end -->

        <!-- 导出 Excel start -->

        <!-- <download-excel class="export-btn" :data="tableData" :fields="tableFields" type="xls" header="合作公司" name="合作公司.xls">
          <el-button type="primary" icon="el-icon-upload">
            导出Excel
          </el-button>
        </download-excel> -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-download"
          @click="exportExcel"
          >导出Excel</el-button
        >

        <!-- 导出 Excel end -->

        <!-- 导入 Excel start -->
        <!-- <el-upload class="upload-demo" action="" :on-change="handleChange" :on-exceed="handleExceed" :on-remove="handleRemove" :file-list="fileListUpload" :show-file-list="false" :limit="limitUpload" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel" :auto-upload="false" v-if="updateUpload">
          <el-button type="primary" icon="el-icon-upload">导入Excel</el-button>
        </el-upload> -->
        <!-- 导入 Excel end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="loadingTable"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <!-- <el-table-column label="操作" min-width="130">
            <template slot-scope="scope">
              <el-button @click.native.prevent="editRow(scope.$index, scope.row)" type="text" size="small">
                编辑
              </el-button>
            </template>
          </el-table-column> -->
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <el-dialog
        class="dialog"
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
      >
        <!-- form表单 -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="100px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
          <!-- 合作公司名称 start -->
          <el-form-item label="合作公司名称" prop="companyName">
            <el-input
              type="text"
              v-model="validateForm.companyName"
              autocomplete="off"
              placeholder="请输入合作公司名称"
            ></el-input>
          </el-form-item>
          <!-- 合作公司名称 end -->

          <!-- 收款账户 start -->
          <el-form-item label="收款账户" prop="collectionAccount">
            <el-input
              type="text"
              v-model="validateForm.collectionAccount"
              autocomplete="off"
              placeholder="请输入收款账户"
            ></el-input>
          </el-form-item>
          <!-- 收款账户 end -->

          <!-- 收款账号 start -->
          <el-form-item label="收款账号" prop="collectionAccountNumber">
            <el-input
              type="text"
              v-model="validateForm.collectionAccountNumber"
              autocomplete="off"
              placeholder="请输入收款账号"
            ></el-input>
          </el-form-item>
          <!-- 收款账号 end -->

          <!-- 开户行 start -->
          <el-form-item label="开户行" prop="bank">
            <el-input
              type="text"
              v-model="validateForm.bank"
              autocomplete="off"
              placeholder="请输入开户行"
            ></el-input>
          </el-form-item>
          <!-- 开户行 end -->

          <!-- 备注 start -->
          <el-form-item label="备注" prop="notes">
            <el-input
              type="text"
              v-model="validateForm.notes"
              autocomplete="off"
              placeholder="请输入备注"
            ></el-input>
          </el-form-item>
          <!-- 备注 end -->
        </el-form>
        <!-- form表单 -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
export default {
  name: 'PartnerCompany',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      // input: '', // 请输入公司名称
      searchCompanyName: '', // 搜索的公司名称
      tableFields: {
        //导出Excel表格的表头设置
        景区名称: 'spotName',
        机器数量: 'robotNumber',
        申请单号: 'applyNumber',
        申请报告: 'APPLICATION_REPORT',
        申请时间: 'applyTime',
        故障描述: 'applyReasion',
        上报人员: 'applyUserName',
        上报电话: 'applyUserPhone',
        评估人: 'assessorId',
        评估报告: 'assessmentReport',
        审批人: 'approverId',
        审批结果: 'EXAMINE_RESULT',
        审批意见: 'examineAggestion',
        处理结果确认人: 'finalId',
        处理结果确认: 'processingResults', // 0未处理，1已修好，2已回收
        入库单号: 'inventoryNumber',
        '变卖款额/元': 'saleAmount',
        机器人编号: 'robotCode',
        备注: 'note',
        详情: 'details'
      },
      tableData: [], // 表格数据
      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      loadingTable: false,

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      searchScenicOptions: [
        // {
        //   spotId: 1,
        //   spotName: '测试景区1'
        // },
        // {
        //   spotId: 2,
        //   spotName: '测试景区2'
        // },
        // {
        //   spotId: 3,
        //   spotName: '测试景区3'
        // }
      ], // 搜索时景区选项

      searchUserOptions: [
        {
          peopleId: 1,
          peopleName: '测试维修人员1'
        },
        {
          peopleId: 2,
          peopleName: '测试维修人员2'
        },
        {
          peopleId: 3,
          peopleName: '测试维修人员3'
        }
      ], // 搜索时维修人员选项

      dialogVisible: false, // 是否显示dialog对话框
      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      dateType: 'daterange', // 日期类型

      startEndDate: [], // 搜索表单起止时间
      searchValidateForm: {
        // robotCode: '', // 机器人编号
        // spotId: '', // 景区id
        // spotName: '', // 景区名称
        // faultId: '', //故障id
        // faultName: '', // 故障名称
        // faultNumber: '', // 故障单号
        // peopleId: '', // 维修人员id
        // peopleName: '', // 维修人员名称
        // startEndDate: '' // 派单起止时间
      }, // 搜索表单验证对象
      validateForm: {},
      total: 0,

      dialogTitle: '',

      limitUpload: 3,
      file: null, // 上传的Excel文件
      fileTemp: null, // 上传的临时Excel文件
      fileListUpload: [], // 上传Excel文件列表

      updateUpload: true, // 是否显示upload组件

      // 提交表单验证规则
      validateFormRules: {
        // 合作公司名称
        companyName: [
          {
            validator: checkRequireMethod('合作公司名称'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 收款账户
        collectionAccount: [
          {
            validator: checkRequireMethod('收款账户'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 收款账号
        collectionAccountNumber: [
          {
            validator: checkRequireNumberMethod('收款账号'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 开户行
        bank: [
          {
            validator: checkRequireMethod('开户行'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      },

      // 搜索表单验证规则
      searchValidateFormRules: {}
    }
  },
  created() {
    this.getSearchValidateForm()
    this.getValidateForm()

    this.searchValidateForm.robotCode = new URL(location.href).searchParams.get(
      'robotCode'
    )

    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.initTableData()
    this.getScenicList()
    this.getSysAppUsers()
  },
  methods: {
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = null
      }
      this.searchValidateForm = {
        ...validateForm,
        robotCode: null, // 机器人编号
        spotId: null, // 景区id
        faultName: null, //故障名称
        faultNumber: null, // 故障单号
        userId: null // 维修人员id
      }
    },

    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = null
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      console.log(this.searchValidateForm)
      this.loadingTable = true
      const { data: res } = await this.$http.get(
        '/system/substitute_apply_system/list',
        {
          params: {
            ...this.searchValidateForm,
            beginDate: this.startEndDate && this.startEndDate[0],
            endDate: this.startEndDate && this.startEndDate[1],
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      // console.log(res)
      if (res.state == '200') {
        this.tableData = res.data.list
        this.total = res.data.total
        this.loadingTable = false
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList() {
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/spotList'
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchScenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      const { data: res } = await this.$http.get(
        '/system/substitute_apply_system/uploadExcelSubstituteApplySystem',
        {
          params: {
            ...this.searchValidateForm,
            beginDate: this.startEndDate && this.startEndDate[0],
            endDate: this.startEndDate && this.startEndDate[1],
          },
          responseType: 'arraybuffer'
        }
      )
      // console.log(res)
      const url = window.URL.createObjectURL(
        new Blob([res], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        })
      )
      const a = document.createElement('a')
      // console.log(url)
      a.href = url
      a.download = '机器人大维修及报废'
      a.click()
      this.downloadLoading = false
    },

    /**
     * 获取维修人员列表
     */
    async getSysAppUsers() {
      const { data: res } = await this.$http.get(
        '/system/appUsers/getSysAppUsers'
      )
      this.searchUserOptions = res.data
    },

    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.initTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.getSearchValidateForm()
      this.getValidateForm()

      this.startEndDate = []

      this.dateType = 'daterange'

      // 重新获取表格数据
      this.initTableData()
    },

    // 导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true
          await this.getExportTableData()
          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
        .catch(() => {
          this.downloadLoading = false
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = 'add'
      this.dialogTitle = '新增合作公司信息'
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row);
      this.dialogVisible = true // 是否显示编辑dialog对话框
      var rowObj = Object.assign({}, row) // 深拷贝row对象
      // rowObj.collectionAccountNumber = parseFloat(rowObj.collectionAccountNumber)
      this.validateForm = rowObj
      this.dialogState = 'edit'
      this.dialogTitle = '编辑合作公司信息'
    },

    // 编辑dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 搜索时选项变化 start
    /**
     * 景区选项变化时触发
     */
    async onChangeSearchSpot(spotId) {
      this.searchScenicOptions.forEach((item) => {
        if (item.SCENIC_SPOT_ID == spotId) {
          this.searchValidateForm.spotName = item.SCENIC_SPOT_NAME
        }
      })
    },

    /**
     * 故障选项变化时触发
     */
    async onChangeSearchUser(userId) {
      this.searchUserOptions.forEach((item) => {
        if (item.userId == userId) {
          this.searchValidateForm.userName = item.userName
        }
      })
    },
    // 搜索时选项变化 end

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          console.log(this.validateForm)
          if (this.dialogState == 'add') {
            // 新增公司信息
            console.log('新增公司信息')
            this.addTableData()
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
            this.editTableData()
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    /**
     * 更新upload组件的方法
     */
    reloadUpload() {
      // this.$forceUpdate()
      // 移除组件
      this.updateUpload = false
      // 在组件移除后，重新渲染组件
      // this.$nextTick可实现在DOM 状态更新后，执行传入的方法。
      this.$nextTick(() => {
        this.updateUpload = true
      })
    },

    // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
    handleChange(file, fileList) {
      // console.log(file);
      this.fileListUpload[0] = file
      this.fileTemp = file.raw

      this.reloadUpload()

      // 导入判断
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
          this.fileTemp.type == 'application/vnd.ms-excel'
        ) {
          this.importfxx(this.fileTemp)
        } else {
          this.$message({
            type: 'warning',
            message: '附件格式错误，请删除后重新上传！'
          })
        }
      } else {
        this.$message({
          type: 'warning',
          message: '请上传附件！'
        })
      }
    },

    // 文件超出个数限制时的钩子
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      )
    },

    // 文件列表移除文件时的钩子
    handleRemove(file, fileList) {
      this.fileTemp = null
      this.fileListUpload = []
    },

    // 导入格式
    importfxx(obj) {
      let _this = this
      // 通过DOM取文件数据
      this.file = obj
      var rABS = false //是否将文件读取为二进制字符串
      var f = this.file
      var reader = new FileReader()
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = ''
        var rABS = false //是否将文件读取为二进制字符串
        var pt = this
        var wb //读取完成的数据
        var outdata
        var reader = new FileReader()
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result)
          var length = bytes.byteLength
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i])
          }
          var XLSX = require('xlsx')
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //手动转化
              type: 'base64'
            })
          } else {
            wb = XLSX.read(binary, {
              type: 'binary'
            })
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]) //outdata就是你想要的东西
          console.log(outdata)
          this.da = [...outdata]
          let arr = []
          this.da.map((v) => {
            let obj = {
              robotCount: v['机器人数量'],
              type: v['发货进度'],
              applicantName: v['申请人'],
              factoryName: v['发货工厂'],
              consignorName: v['发货人'],
              receivingName: v['收货景区'],
              consigneeName: v['收货人'],
              consigneePhone: v['收货人电话'],
              receiningAddress: v['收货地址'],
              consignmentDate: v['发货时间']
            }
            arr.push(obj)
          })
          // // 将 arr 赋值给 tableData，来更新表格数据
          // this.tableData = arr
          return arr
        }
        reader.readAsArrayBuffer(f)
      }

      if (rABS) {
        reader.readAsArrayBuffer(f)
      } else {
        reader.readAsBinaryString(f)
      }
    }
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        var result = ''
        if (value == 'collectionAccountNumber') {
          // 四个数字空一格
          var str = String(row[value])
          var len = 4
          var arr = []
          for (var i = 0; i < str.length; i += len) {
            arr.push(str.slice(i, i + len))
          }
          // console.log(arr);
          result = arr.join(' ')
        } else if (value == 'processingResults') {
          if (row[value] == 0) {
            result = '未处理'
          } else if (row[value] == 1) {
            result = '已修好'
          } else if (row[value] == 2) {
            result = '已回收'
          }
        } else {
          result = row[value] || '无'
        }
        return result
      }
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  }
}
</script>

